export default {
	data() {
		return {
			userinfo: {}
		};
	},
	async onShow() {
		// 获取用户头像 昵称
		if(!getApp().globalData.userinfo){
			let userinfo = await this.ajax(this.api.userinfo)
			if (userinfo.data.code == 200) {
				getApp().globalData.userinfo = userinfo.data.data
				this.userinfo = getApp().globalData.userinfo
			}
		}else{
			this.userinfo = getApp().globalData.userinfo
		}
	},
	methods: {
		onMenu(url) {
			if(!this.userinfo){
				uni.showToast({
					title: '请先登录!',
					duration: 2000,
					icon: 'none'
				})
				return false
			}
			uni.navigateTo({
				url: url
			})
		},
		// 获取用户信息并登陆
		async wxGetUserInfo(e) {
			console.log(e)
			uni.showLoading({
				title: '登陆中...',
				mask: true
			})
			if (e.detail.iv || e.detail.encryptedData) {
				// 获取code
				let [error, data] = await uni.login({
					provider: 'weixin'
				})
				// 登陆
				let res = await this.ajax(this.api.login, {
					code: data.code,
					iv: e.detail.iv,
					encryptedData: e.detail.encryptedData,
					pid: getApp().globalData.pid
				}, 'POST')
				uni.hideLoading()
				if (res.data.code == 200) {
					// 同意授权
					e.detail.userInfo
					let userinfo = {
						avatarurl:e.detail.userInfo.avatarUrl,
						nickname:e.detail.userInfo.nickName 
					}
					getApp().globalData.userinfo = userinfo
					this.userinfo = userinfo
					uni.setStorageSync('token', res.data.data)
					uni.showToast({
						title: '登陆成功!',
						duration: 2000,
						icon: 'none'
					})
					//检测用户是否完善信息并获取用户的uid
					let info = await this.ajax(this.api.isinfo)
					if (info.data.code == 200) {
						getApp().globalData.uid = info.data.data
						console.log('获取到了用户的uid---------'+getApp().globalData.uid)
					}
					
				} else {
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel: false
					})
				}
			}
		},
	}
}
